import React from 'react';
import {Navbar, NavbarBrand, NavbarToggler, Collapse, Nav, NavItem, NavLink} from 'reactstrap';

import logoHyperium from '../images/hyperium-small.svg';
import logoGithub from '../images/github.svg';
import logoTwitter from '../images/twitter.svg';
import logoDiscord from '../images/discord.svg';
import './AppNavbar.css';

class AppNavbar extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    };
  }

  toggle = () => this.setState({
    isOpen: !this.state.isOpen
  });

  render() {
    return <Navbar expand="sm" color="light" light fixed="top">
      <NavbarBrand className="mr-0 mr-md-2" href="/">
        <img src={logoHyperium} alt="Hyperium Logo" width="36" height="36"/>
      </NavbarBrand>
      <NavbarToggler onClick={this.toggle} />
      <Collapse isOpen={this.state.isOpen} navbar>
        <Nav navbar>
          <NavItem active>
            <NavLink href="#home">Home</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#features">Features</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#about">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#contact">Contact</NavLink>
          </NavItem>
        </Nav>
        <Nav className="ml-auto" navbar>
          <NavItem>
            <NavLink href="https://github.com/HyperiumClient/Hyperium/" target="_blank">
              <img width="16" src={logoGithub} alt="GitHub"/>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="https://twitter.com/HyperiumClient/" target="_blank">
              <img width="16" src={logoTwitter} alt="Twitter"/>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="https://discordapp.com/invite/8GakFcT/" target="_blank">
              <img width="16" src={logoDiscord} alt="Discord"/>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink className="btn btn-downloads ml-md-2" href="#downloads">
              Downloads
            </NavLink>
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>;
  }

}

export default AppNavbar;



